import {
	mapState,
	mapGetters
} from 'vuex'
export default {
	data() {
		return {
			focus: false,
			city: "昆明市",
			address: "null",
			isLocation: false,
			locationError: false,
		};
	},
	onLoad: function() {
		// uni.showLoading({
		// 	title: "加载中...",
		// 	mask: true
		// });
	},
	onReady() {
		uni.hideLoading();
	},
	computed: {
		...mapState(["userLocationName", "allAddres", "userLocationArr","jsAddres"]),
		...mapGetters(['getUserLocName'])
	},
	methods: {
		// 空函数 阻止穿透
		moveHandle(){},
		//搜索
		input(val) {
			console.log("搜索", val.target.value)
			if (val.target.value.length === 0) {
				this.focus = false
			}
			this.$store.dispatch('getParameters', val.target.value) //调用actions
		},
		focusfun() {
			this.focus = true
		},
		// 开始获取定位数据,选择地址
		_getCurLocation() {
			this.$store.dispatch('getjX')
		},
		// 选择我定义的地址
		_chooseMyLocation(e) {
			this.$store.commit('getUserLocation', {
				name: e.address,
				location: e.location,
			})
		},
		// 选择附近地址
		_choosePoisLocation(e) {
			this.$store.commit('getUserLocation', {
				name: e.name,
				location: e.location,
			})
		},
		_choosejsLocation(e){
			this.$store.commit('chooseAddress', {
				name: e.name,
				location: e.location,
			})
		}
	}
};
